import React from 'react';
import {connect} from 'react-redux';
import {Button} from "reactstrap";

const MaxISP = ({name}) => {
    return <Button> HUUUNDfurziiI {name}</Button>

};


const mapStateToProps = state => {
    return {
       name: state.auth.token
    };
};

const mapDispatchToProps = {};

export default connect(mapStateToProps, mapDispatchToProps)(MaxISP);




